.header_topbar{
    font-family: 'Rubik', sans-serif;
    background: #fff;
	font-weight:400;
    border-bottom: 1px solid #e7e7e7;
}
.header_topbar .links li{list-style:none; display:inline-block; line-height:0px; margin-right:10px; border-right: 1px solid #b8b8b8; padding:0px 10px;}
.header_topbar .links .flaticon{
	color: #1b1b1b;
	font-size: 14px;
    margin-right: 4px;
}
.header_topbar .links li a{
	font-family: 'Rubik', sans-serif;
    font-weight: 400;
	font-size: 15px;
    color: #1b1b1b;
}
