.slider .mt15{margin-top: 15px;;}
.slider img{
    -webkit-box-shadow: 0 5px 83px 0 rgb(0 0 0 / 40%);
    box-shadow: 0 5px 83px 0 rgb(0 0 0 / 40%);
    border: 1px solid #eee;
    border-radius: 6px !important;
}
.carousel-caption{
	top:15%;
	position:absolute;
	left:4%;
	text-align:left;
}
.carousel-caption h6 span{
    font-family: 'Rubik', sans-serif;
    background: #f3a952;
	color: #fff;
    font-size: 15px;
    padding: 5px 15px;
    border-radius: 5px;
    font-weight: 400;
	line-height:25px;
}
.carousel-caption h1{
	font-family: 'Rubik', sans-serif;
	position: relative;
    color: #1b1b1b;
